import './styles.scss'
import Button from '../components/Button/Button';
import { Fragment } from 'react';
import Head from 'next/head';
import Router from 'next/router';
import StyledInput from '../components/Input/Input';
import { Card } from '@material-ui/core';

const Index = () => {

    return (
        <Fragment>
            <Head>
                <title>JusCredit - Home</title>
            </Head>
            <main>
                <p>
                <div className="map-container"> 
                    <div className="map" />
                     <h1>Antecipe seu </h1>
                         <h1>crédito trabalhista</h1><h2>Têm um processo ganho? Receba agora</h2>       
                    <div className="buttons-container">
                    <Button onClick={()=> Router.push('/cadastro/cliente')} color="primary">Quero antecipar</Button> 
                   <Button onClick={()=> Router.push('/cadastro/investidor')} color="secondary">Quero investir</Button>
    
                <div className="cursor-container">
                 <Button color="third" ><img src="/cursor.svg"/>Conheça as vantagens</Button>
                        </div>
                </div>
                </div>
                <div className="antecipar">
            
                    <div> <h1>Mais valor para quem vende<br/>
                              Simples para quem investe</h1> 
               <h2>
                   Vantagens para quem vende:
               </h2>
               <Card className ="card-vantagem">

                   <img src="agility.svg"></img>
                   <h3>Agilidade</h3>
                   <h4>•Liquidez imediata. <br/>
                       •Não espere para receber o seu direito.<br/>
                       •Receba direto na sua conta bancaria.
                        </h4> 
                        <br/>

                        </Card>
                         <Card className ="card-vantagem">
                   <img src="praticity.svg"></img>
                   <h3>Praticidade </h3>
                   <h4>•Processo de antecipação 100% digital <br/> 
                       •Análise rápida e sem custo</h4> <br/> 
                        </Card>                      
                        
                         <Card className ="card-vantagem">
                   <img src="liberty.svg"></img>
                   <h3>Liberação</h3>
                   <h4>A JusCredit é uma instituição autorizada <br/> 
                       e regulada pelo Banco Central do Brasil (Bacen),<br/>  
                       atuando como correspondente bancário.</h4> <br/> 
                        </Card>    
                        </div>  
                        
              <h2> Vantagens para quem investe:</h2>       

             
               </div>
                             
                    <div className="funciona-container">
                    <div className="poligono-container"/>
                        <h1>Como funciona a plataforma?</h1>
                        <h2>Conte com toda a segurança, agilidade <br/>
                           e praticidade na liberação do seu processo ganho.
                        </h2>  
                        <img src="/trabalhador.svg"/>  
                        </div>
                <div className="duvidas-container">   
                    <h1>Têm dúvidas ?</h1>
                     <h2>Queremos te ajudar a resolvê-las</h2>
                    <div className="buttons-container"> 
                    <Button onClick={()=> Router.push('/cadastro/cliente')} color="secondary">Quero antecipar</Button>
</div>              <img src="/duvidas.svg"/>
                    
                    </div> 

                    <div className= "parceiros-container">
                        <h1> Seja um parceiro JusCredit</h1>
                        <h2>Indique créditos trabalhistas elegíveis e ganhe conosco.</h2>
                        <StyledInput mask={null} placeholder="   Insira aqui seu e-mail" tabIndex={-1}/>
<Button onClick={()=> Router.push('/parceiro')}color="fourth" tabIndex={-1}>Enviar</Button>
</div>
                <br/>
                </p>
            </main>
        </Fragment >

    );
}

export default Index;